// Главная функция инициализации
async function bootstrap() {
    try {
        // 1. Инициализируем SDK
        await init(); // Ждем завершения инициализации

        // 2. Расширяем вьюпорт и включаем полноэкранный режим
        await expandViewport();
        await requestFullscreen();

        // 3. Монтируем React-приложение
        const root = createRoot(document.getElementById('root')!);
        root.render(<App />);

    } catch (error) {
        console.error('Initialization failed:', error);
    }
}

// Запускаем процесс
bootstrap();
